	module.exports = {"data":{"markdownRemark":{"html":"<p>こんなコーナー始めといてなんですが、最近「起伏」がないです。最近の生活と言ったら起きて飯食って部活の夏練習行って飯食ってゲーム作って飯食ってバイト行って飯食って寝て……の無限ループだったりします。あえて言うなら私がバイトで働いているコンビニが停電した程度。ああ、そうだその話でも書こう。(最高に行き当たりばったりでｽﾏｿ)  金曜日の午前2時、強烈な局所的雷雨でコンビニ停電。一瞬真っ暗になるが UPS が効いているのか補助燈が灯り、ATM とレジだけは相変わらず作動している。停電しているにもかかわらずレジが打てるのにはちょいと感動した。まぁ停電の最中にわざわざモノを(しかも少年マガジンを)買いに来る客も客だが。 その時来たいつも本を運んでいるトラック運転手に聞いたところ「ここいら一帯全部停電していた」そうだ。なんか信号機や街灯なども停電していたっぽい。30分もしないうちに停電から回復したが、サージで多数の機器が逝かれたらしく業者が十数人も来てごったがえしていたのが印象的だった。でも独りシフトだったら怖かったろうなぁ。いや、おばけじゃなくて闇討ちとかが（笑 話が飛びすぎたのでちょいと当サイトの小話を。当サイトは 1999 年初頭に初心者向け DDR 攻略サイトとして生まれ幾度のリニューアルや移転を経てついに8月に独自ドメイン取得に至りました。 ところで独自ドメインって、目立つ以外に効果あるの？と疑問に思われている方へ、お答えします。断言しますが、ばっちり効果あります。1 日に来るウィルスメールが <strong>3 倍</strong>(従来比)に増えます。以上。</p>","frontmatter":{"cover":null,"date":"2002-08-19T00:00:00+09:00","strDate":"2002/8/18","title":"つぎに"}}},"pathContext":{"path":"/ja/blog/2002/08/19-つぎに/","langKey":"ja"}}

